.Board {
    position: relative;
    -ms-flex-line-pack: center;
        align-content: center;
    margin: auto;
    max-width: 100%;
    background-color: #000;
    background-image:
        -webkit-linear-gradient(#333 1px, transparent 1px),
        -webkit-linear-gradient(left, #333 1px, transparent 1px);
    background-image:
        -o-linear-gradient(#333 1px, transparent 1px),
        -o-linear-gradient(left, #333 1px, transparent 1px);
    background-image:
        linear-gradient(#333 1px, transparent 1px),
        linear-gradient(90deg, #333 1px, transparent 1px);
    z-index: 0
}

.Cell {
    background: rgb(12, 61, 83);
    position: absolute;
    z-index: 1;
}

.controls {
    margin-top: 20px;
}

.button {
    margin-left: 12px;
    cursor: pointer;
}


